<template>
  <div class="form-chitiet">
    <div class="row-header">
      <div class="background-triangle-big"> <span>BÁO CÁO</span> </div>
      <div class="layout row wrap header_tools row-blue">
        <div class="flex xs12 pl-3 text-ellipsis text-bold">
          <v-layout wrap class="chart__report">
            <v-flex xs6 sm2 class="px-2">
              <v-select 
                :items="years" 
                v-model="year"
                autocomplete 
                item-text="name" 
                item-value="value"
                :hide-selected="true"
                @change="changeYear"
                >
              </v-select>
            </v-flex>
            <v-flex xs6 sm2 class="px-2">
              <v-select 
                :items="months" 
                v-model="month"
                autocomplete 
                item-text="name" 
                item-value="value"
                :hide-selected="true"
                @change="changeMonth"
                >
              </v-select>
            </v-flex>
            <v-flex xs6 sm8 class="px-2">
              
            </v-flex>
          </v-layout>
        </div>
      </div>
    </div>
    <vue-friendly-iframe v-if="pdfBlob !== null && pdfBlob !== undefined && pdfBlob !== '' " :src="pdfBlob"></vue-friendly-iframe>
    <v-layout row wrap v-else>
      <v-flex xs12 class="text-xs-center" mt-5>
        <v-progress-circular
          :size="100"
          :width="1"
          color="primary"
          indeterminate
        ></v-progress-circular>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import router from '@/router'
import VueFriendlyIframe from 'vue-friendly-iframe'
import support from '../store/support.json'

export default {
  props: ['index', 'id'],
  components: {
    'vue-friendly-iframe': VueFriendlyIframe
  },
  data: () => ({
    danhSachBaoCaos: [],
    years: [
      {
        'value': '',
        'name': 'Lọc theo năm'
      },
      {
        'value': '2017',
        'name': 'năm 2017'
      },
      {
        'value': '2018',
        'name': 'năm 2018'
      },
      {
        'value': '2019',
        'name': 'năm 2019'
      }
    ],
    year: (new Date()).getFullYear() + '',
    months: [
      {
        'value': '0',
        'name': 'Lọc theo tháng'
      },
      {
        'value': '1',
        'name': 'tháng 1'
      },
      {
        'value': '2',
        'name': 'tháng 2'
      },
      {
        'value': '3',
        'name': 'tháng 3'
      },
      {
        'value': '4',
        'name': 'tháng 4'
      },
      {
        'value': '5',
        'name': 'tháng 5'
      },
      {
        'value': '6',
        'name': 'tháng 6'
      },
      {
        'value': '7',
        'name': 'tháng 7'
      },
      {
        'value': '8',
        'name': 'tháng 8'
      },
      {
        'value': '9',
        'name': 'tháng 9'
      },
      {
        'value': '10',
        'name': 'tháng 10'
      },
      {
        'value': '11',
        'name': 'tháng 11'
      },
      {
        'value': '12',
        'name': 'tháng 12'
      }
    ],
    month: '0',
    danhSachBaoCao: [],
    pdfBlob: null
  }),
  computed: {
    loadingMenuConfigToDo () {
      return this.$store.getters.loadingMenuConfigToDo
    }
  },
  created () {
    var vm = this
    vm.$nextTick(function () {
      vm.danhSachBaoCao = vm.loadingMenuConfigToDo
      vm.doPrintReport()
    })
  },
  updated () {
    var vm = this
    vm.$nextTick(function () {
    })
  },
  watch: {
    '$route': function (newRoute, oldRoute) {
      let vm = this
      let currentQuery = newRoute.query
      vm.doPrintReport()
    }
  },
  methods: {
    doPrintReport () {
      let vm = this
      let documentTYPE = vm.danhSachBaoCao[vm.index].document
      let filter = {
        document: documentTYPE,
        year: vm.year,
        month: vm.month
      }
      vm.pdfBlob = null
      vm.$store.dispatch('getAgencyReportLists', filter).then(function (result) {
        let filterPostData = {
          document: documentTYPE,
          data: support.fakeReport
        }
        console.log('filterPostData', filterPostData)
        vm.$store.dispatch('doStatisticReportPrint', filterPostData).then(function (result) {
          vm.pdfBlob = result
        })
      })
    },
    changeYear (item) {
      let vm = this
      vm.year = item
      vm.doPrintReport()
    },
    changeMonth (item) {
      let vm = this
      vm.month = item
      vm.doPrintReport()
    }
  }
}
</script>



